import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
// import pages
import Home from './pages/Home';
import About from './pages/About';
import SingleAlbum from './pages/SingleAlbum';
import Error from './pages/Error';
import Artist from './pages/Artist';
// import components
import Navbar from './components/Navbar';

//deployed home page domain
const deployedDomain = '/AlbumLibrary-react';

function App() {
  return (
    <Router>
      <Navbar />
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path={`${deployedDomain}`}>
          <Home />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/album/:id">
          <SingleAlbum />
        </Route>
        <Route path="/artist/:id">
          <Artist />
        </Route>
        <Route path="*">
          <Error />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
